import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import FindPerson from '@/components/FindPerson'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/hello',
      name: "HelloWorld",
      component: HelloWorld
    },
    {
      path: '/find',
      name: "FindPerson",
      component: FindPerson
    }
  ]
})



