<template>
  <div>
    <Layout style="width: 600px; height: 250px;">
      <LayoutPanel headerCls="main-header" style="height: 100%;" region="center">
        <template slot="header">
          <svg class="side-toggle" viewBox="0 0 24 24" >
            <path d="M3 18h18v-2H3v2zm0-5h18v-2H3v2zm0-7v2h18V6H3z"></path>
          </svg>
          <span>Hello World</span>
        </template>
        <Form ref="form" :model="user" class="form" :rules="rules" @validate="errors=$event">
          <Label for="name">Name: </Label>
          <TextBox inputId="name" name="name" v-model="user.name" v-Tooltip="getTipOpts('name')"></TextBox>
          <LinkButton :disabled="false" @click="submitForm()">submit</LinkButton>
        </Form>
        <div style="margin-top:50px;text-align:center;font-size:16px">
          <p>{{msg}}</p>
        </div>
      </LayoutPanel>
    </Layout>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        msg: "Some Messages",
        user: { name: null },
        rules: {
          name: ["required", "length[1,10]"]
        },
        errors: {}
      }
    },
    methods: {
      getError(name) {
        return this.errors[name] && this.errors[name].length
          ? this.errors[name][0]
          : null;
      },
      hasError(name) {
        return this.getError(name) != null;
      },
      getTipOpts(name) {
        return {
          content: this.getError(name),
          closed: !this.hasError(name),
          position: "right"
        };
      },
      submitForm() {
        this.$axios.get('/ribbon/sayHello',{
          params: {user: this.$data.user.name}
        }).then(res => {
          this.$data.msg = res.data
        }).catch(err => {
          console.info(err.response.message)
        });
      }
    }
  }
</script>
<style>
  .main-header {
    color: #222;
    line-height: 30px;
  }
  .side-toggle {
    position: relative;
    display: inline-block;
    width: 24px;
    height: 24px;
    top: 6px;
    left: 5px;
    margin-right: 10px;
    color: #000;
    fill: #000;
    cursor: pointer;
  }
  .form {
    text-align: center;
    margin-top: 10px;
  }
</style>



