<template>
  <div>
    <Layout style="width: 700px; height: 300px;">
      <LayoutPanel headerCls="main-header" style="height: 70px;" region="north">
        <Form ref="form" :model="person" class="submit-form">
          <div class="title">
            <Lable for="name">name:</Lable>
            <TextBox inputId="name" name="name" v-model="person.name"
             iconCls="icon-man"></TextBox>
          </div>
          <div class="title">
            <Label for="gender">gender:</Label>
            <ComboBox inputId="gender" name="gender" :data="gender" :editable="false"
             v-model="person.gender" :panelStyle="{height:'auto'}"></ComboBox>
          </div>
          <div class="title">
            <LinkButton :disabled="false" @click="findPerson()">Find Person</LinkButton>
            <LinkButton :disabled="false" @click="showMe()">Show Me</LinkButton>
          </div>
        </Form>
      </LayoutPanel>
      <LayoutPanel region="center" style="height: 80%;margin-top:20px;">
        <Form style="margin-top: 10px; margin-left: 10px;">
          <div class="deplay-div">
            <Label>id:</Label>
            <Label>{{result.id}}</Label>
          </div>
          <div class="deplay-div">
            <Label>name:</Label>
            <Label>{{result.name}}</Label>
          </div>
          <div class="deplay-div">
            <Label>gender:</Label>
            <Label>{{result.gender}}</Label>
          </div>
        </Form>
      </LayoutPanel>
    </Layout>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        person: {
          id: 0,
          name: null,
          gender: null
        },
        gender: [
          {value: "male", text: "male"},
          {value: "famale", text: "famale"}
        ],
        result: {
          id: "",
          name: "",
          gender: ""
        }
      }
    },
    methods: {
      findPerson() {
        this.$axios({
          url: '/ribbon/findPerson',
          method: 'post',
          data: this.$data.person
        }).then(rep => {
          this.$data.result = rep.data
        }).catch(err => {
          console.info(err)
        })
      },
      showMe() {
        this.$axios.get('/ribbon/showMe').then(rep => {
          this.$data.result = rep.data
        }).catch(err => {
          console.info(err)
        })
      }
    }
  }
</script>

<style>
  .title {
    margin-top: 10px;
    margin-bottom:10px;
    display:inline-block;
  }
  .submit-form {
    margin-top: 10px;
    margin-left: 10px;
  }
  .deplay-div {
    margin-bottom:20px;
    margin-left: 20px;
  }
</style>



